<template>
  <div class="loginContainer">
    <div class="welcome">欢迎您，请登录</div>
    <div class="inputContent">
      <form action="">
        <div class="inputItem">
          <span class="tit">手机号码</span>
          <input
            v-model="phone"
            type="text"
            placeholder="请输入手机号码"
            autocomplete="“off”"
          />
        </div>
        <div class="inputItem">
          <span class="tit">密码</span>
          <input
            v-model="password"
            type="password"
            placeholder="请输入密码"
            autocomplete="“off”"
          />
        </div>
      </form>
      <button class="confirmBtn" @click="loginEvent">登录</button>
      <button class="cancelBtn" @click="$router.go(-1)">取消</button>
      <div class="forgetSection">忘记密码?</div>
    </div>
    <div class="registerSection">
      还没有账号?
      <span>马上注册</span>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
import { ElMessage } from 'element-plus'
import { refreshLogin } from '@/tools/request'
export default {
  name: 'Login',
  data() {
    return {
      phone: '',
      password: '',
    }
  },
  methods: {
    async loginEvent() {
      if (this.phone === '') {
        ElMessage.error('手机号未输入哦!')
        return
      }
      let phoneReg = /^1[3-9]\d{9}$/
      if (!phoneReg.test(this.phone)) {
        ElMessage.error('手机号格式错误哦!')
        return
      }
      if (this.password === '') {
        ElMessage.error('密码未输入哦!')
        return
      }

      let result = await this.$store.dispatch('Login', {
        phone: this.phone,
        password: this.password,
      })
      if (result.data.code == 200) {
        this.$router.push('/personal')
      } else if (result.data.code == 502) {
        ElMessage.error('账号或者密码错误哦!')
      }
    },
    ...mapMutations(['setUser']),
  },
}
</script>
<style lang="less" scoped>
.loginContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 7.5rem;
  height: 100vh;
  background-color: #fff;
  .welcome {
    font-size: 0.45rem;
    color: #555;
    padding: 1rem 0.5rem;
  }
  .inputContent {
    flex: 1;
    padding: 0 0.5rem;
    .inputItem {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      background: #f8f6fc;
      border-radius: 15px;
      box-shadow: 0 0 60px 0px rgba(151, 151, 151, 0.4);
      margin-bottom: 0.5rem;
      padding: 0.3rem;
      .tit {
        font-size: 0.35rem;
        margin-bottom: 0.2rem;
        color: #555;
      }
      input {
        width: 100%;
        outline: none;
        border: none;
        background: none;
        transition: all 0.3s;
      }
      input:focus {
        font-size: 0.3rem;
      }
    }
  }
  .confirmBtn {
    background: #d43c33;
    color: #fff;
    width: 100%;
    padding: 0.25rem;
    border-radius: 1rem;
    border: none;
    font-size: 0.3rem;
    margin-bottom: 0.3rem;
    transition: all 0.3s;
  }
  .confirmBtn:active {
    opacity: 0.8;
  }
  .cancelBtn {
    background: rgb(241, 241, 241);
    color: rgb(0, 0, 0);
    width: 100%;
    padding: 0.25rem;
    border-radius: 1rem;
    border: 1px solid rgb(218, 218, 218);
    font-size: 0.3rem;
    transition: all 0.3s;
  }
  .cancelBtn:active {
    opacity: 0.6;
  }
  .forgetSection {
    color: #4399fc;
    text-align: center;
    margin-top: 0.3rem;
    font-size: 0.28rem;
  }
  .registerSection {
    width: 100%;
    color: #606266;
    text-align: center;
    margin-bottom: 0.2rem;
    span {
      color: #4399fc;
    }
  }
}
</style>
